import moment from 'moment'
import JsonViewer from '@/components/JsonViewer'
import { required } from 'vuelidate/lib/validators'
import VueNumeric from 'vue-numeric'
export default {
    name: 'loan-b-create',
    data() {
        return {
            engagementOptions: [],
            rEngagements: [],
            errorMessage: '',
            loan: {
                caseId: '',
                amount: '',
                createdDate: moment(new Date()).format('YYYY-MM-DD'),
                repaymentDate: ''
            }
        }
    },
    validations: {
        loan: {
            caseId: { required },
            amount: { required },
            repaymentDate: { required }
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        getEngagements() {
            this.$http({
                method: 'get',
                url: '/engagement/manage-engagement/list-of-engagement',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                let rEngagements = response.data.filter(engagement => engagement.status == 'a')
                this.rEngagements = rEngagements
                for (let i = 0; i < rEngagements.length; i++) {
                    let rEngagement = {
                        value: rEngagements[i].case_id,
                        text: rEngagements[i].client_name + ' (' + rEngagements[i].case_id + ')'
                    }
                    this.engagementOptions.push(rEngagement)
                }
            })
            .catch(error => {
                this.errorMessage = error.response.data
            })
        },
        handleModalConfirmationOk() {
            this.$bvModal.show('modal-loading')
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: '/loan/manage-loan/loan-b',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        case_id: this.loan.caseId,
                        loan_type: 'b',
                        loan_amount: this.loan.amount,
                        created_date: this.loan.createdDate,
                        repayment_date: this.loan.repaymentDate
                    }
                })
                .then(response => {
                    this.$bvModal.hide('modal-loading')
                    this.$router.push('/admin/loan-b')
                })
                .catch(error => {
                    this.$bvModal.hide('modal-loading')
                    this.errorMessage = error.response.data.message
                })
            }, 1000)
        },
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.errorMessage = 'Please complete mandatory fields'
            } else {
                this.$bvModal.show('modal-confirmation')
            }
        },
        resetErrorMessage() {
            this.errorMessage = ''
        }
    },
    created() {
        this.getEngagements()
    }
}